.coin-app{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 64px;
    color: #fff;
}
  
.coin-search{
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
  
.coin-text {
    margin-bottom: 32px;
    text-align: center;
}
  
.coin-input{
    padding-left: 16px;
    width: 300px;
    height: 50px;
    border-radius: 4px;
    border: none;
    background-image:
    linear-gradient(to right, #ac32e4, #7918f2, #4801ff);
    color: #e2e2e2;
}
  
.coin-input::placeholder{
    color: #e2e2e2;
}

.coin-title{
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 400px;
}

.coin-title h1{
    font-size: 16px;
    width: 150px;
}

@media screen and (min-width: 720px) and (max-width: 1024px){
    .coin-title{
        min-width: 200px;
    }
    .coin-title h1{
        font-size: 14px;
    }
}

@media screen and (max-width: 720px){
    .coin-title{
        min-width: 100px;
    }
    .coin-title h1{
        font-size: 12px;
    }
}